
body, html {
  padding: 0;
  margin: 0;
  height: 100%;
}
body {
  padding: 0;
  margin: 0;
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: #122447;
  font-family: 'Josefin Slab' , "Segoe UI" , Roboto , "Segoe UI" , "Helvetica Neue", "PingFang SC", "Noto Sans" , "Noto Sans CJK SC" , "Microsoft YaHei" , sans-serif;
}
input {
  font-family: 'Josefin Slab' , "Segoe UI" , Roboto , "Segoe UI" , "Helvetica Neue", "PingFang SC", "Noto Sans" , "Noto Sans CJK SC" , "Microsoft YaHei" , sans-serif;
}
#loading {
  position: fixed;
  top: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background: #1C89F5;
  -webkit-animation: 1s loading ease-in-out infinite;
          animation: 1s loading ease-in-out infinite;
  display: none;
}
#loading.show {
  display: block;
}
@-webkit-keyframes loading {
0% {
    opacity: 0;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
40% {
    opacity: 1;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
60% {
    opacity: 1;
    -webkit-transform-origin: right;
            transform-origin: right;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
100% {
    opacity: 0;
    -webkit-transform-origin: right;
            transform-origin: right;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
}
@keyframes loading {
0% {
    opacity: 0;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
40% {
    opacity: 1;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
60% {
    opacity: 1;
    -webkit-transform-origin: right;
            transform-origin: right;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
100% {
    opacity: 0;
    -webkit-transform-origin: right;
            transform-origin: right;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
}
#logo {
  font-weight: 300;
  margin-bottom: 40px;
  background: url("./assets/logo.svg") no-repeat center;
  background-size: 50px;
  text-indent: 999999999px;
  padding: 40px 0;
}
#app {
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
}
#app .login {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-flex: 3;
        -ms-flex: 3;
            flex: 3;
}
#app .oauth {
    margin-bottom: 30px;
}
#app .oauth p {
      color: #ddd;
}
#app .oauth a {
      display: inline-block;
      text-decoration: none;
      transition: -webkit-transform .1s ease-in-out;
      transition: transform .1s ease-in-out;
      transition: transform .1s ease-in-out, -webkit-transform .1s ease-in-out;
      width: 50px;
}
#app .oauth a:hover {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
}
#app .oauth a img {
        width: 100%;
        height: auto;
}
#app .or {
    margin-bottom: 30px;
}
#app .signin input {
    border: none;
    padding: 10px 15px;
    font-size: 16px;
    margin-bottom: 10px;
    outline: none;
    border-radius: 5px;
    display: block;
}
#app .signin input[type=text], #app .signin input[type=password] {
      background: #ebf5ed;
      color: #31541a;
      min-width: 300px;
      border: 1px solid transparent;
}
#app .signin input[type=text]:focus, #app .signin input[type=password]:focus {
        border-color: #5DC517;
}
#app .signin input[type=submit] {
      background: #5dc517;
      background-image: linear-gradient(-225deg, #C9E939 0%, #5DC517 100%);
      color: #fff;
      width: 200px;
      margin: 0 auto;
      margin-top: 20px;
      box-shadow: 0 5px 10px rgba(1, 2, 0, 0.2);
      transition: box-shadow .2s ease-in-out;
}
#app .signin input[type=submit]:hover {
        box-shadow: 0 10px 20px rgba(1, 2, 0, 0.2);
}
#app .signin input[type=submit]:active {
        -webkit-filter: brightness(1.05);
                filter: brightness(1.05);
}
#app .signin input[type=submit]:focus {
        border-color: #5DC517;
}
#app .profile {
    text-align: center;
}
#app .profile img {
      width: 150px;
      height: 150px;
      border-radius: 50%;
}
#app .profile p {
      font-size: 30px;
      margin: 40px;
}
#app .profile .signout {
      text-decoration: none;
      background: #ebf5ed;
      color: #848881;
      display: inline-block;
      padding: 5px 10px;
      border-radius: 5px;
      font-size: 14px;
}
.footer {
  padding: 20px 0;
  text-align: center;
  color: #ddd;
}



